.header,
.header--home {
  height: 230px;
  width: 100%;
  padding-top: 20px;
  background-color: #000000b0;
  background-image: url(../images/header@1x.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  overflow-x: hidden;
  @include retinabg {
    background-image: url(../images/header@2x.png);
  }
}

.theme-dark .header {
  background-image: url(../images/header-dark@1x.jpg);
  @include retinabg {
    background-image: url(../images/header-dark@2x.jpg);
  }
}

.header.header--library {
  background-image: url(../images/header-lib-overlay.png),
    url(../images/header-lib@1x.png);
  @include retinabg {
    background-image: url(../images/header-lib-overlay.png),
      url(../images/header-lib@2x.png);
  }
}

.navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.navigation__name {
  display: none;
  color: #ffffff;
  margin-left: 8px;
  font-size: 30px;
  line-height: 1.17;
  font-weight: 500;

  @include tablet {
    display: inline-block;
  }
}
.navigation__actions {
  display: flex;
}
.navigation__page {
  position: relative;
  display: block;
  padding: 20px 0;
  margin-right: 40px;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none;
  font-size: 12px;
  line-height: 1.17;
  font-weight: 500;
  overflow: hidden;

  // &:hover {
  //   text-shadow: 0px 0px 10px #fff, 0px 0px 10px #fff, 0px 0px 25px #fd1b1b,
  //     0px 0px 25px #fd1b1b, 0px 0px 25px #fd1b1b, 0px 0px 25px #fd1b1b;
  // }

  &::after {
    position: absolute;
    left: 0;
    bottom: 17px;
    content: '';
    height: 3px;
    width: 100%;
    background-color: #ff001b;
    transform: translate(105%);
    transition: 250ms linear;

    .theme-dark & {
      background-color: var(--accentDarkTheme);
    }
  }

  &:last-child::after {
    transform: translate(-105%);
  }

  &:last-child {
    margin-right: 0px;
  }

  &.current::after {
    transform: translate(0);
    // transition: 250ms linear;
  }

  &:last-child.current::after {
    transform: translate(0);
  }

  &.current {
    pointer-events: none;
  }
}

.header .container {
  position: relative;
  height: 100%;
}

.header__panel {
  position: absolute;
  right: 20px;
  bottom: 20px;
  display: flex;

  @include tablet {
    right: 32px;
  }
}
