.navigation__link {
  display: flex;
  align-items: center;
}
.logo__text {
  display: inline-block;
  font-size: 10.5em;
  width: 141px;
  height: 34px;
  margin-left: 3px;

  @media screen and (max-width: 400px) {
    display: none;
  }
}

.text-copy {
  fill: none;
  stroke: #ffffff;
  stroke-dasharray: 6% 29%;
  stroke-width: 5px;
  stroke-dashoffset: 0%;
  animation: stroke-offset 5.5s infinite linear;
}

